.page {
  height: 100%; }

.page__body {
  position: relative;
  font-family: "Obviously", "Arial", sans-serif;
  color: #333333;
  font-size: 16px;
  line-height: 28px;
  background-color: #f2f2f2; }

.header {
  margin: 0;
  padding: 0;
  background-color: white;
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  color: #000000;
  font-size: 18px;
  line-height: 40px;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 10px;
  padding-bottom: 10px; }
  .header__logo {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: inherit;
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%; }

@media (min-width: 1200px) {
  .header {
    max-width: 1600px; } }

.footer {
  margin: 0;
  padding: 0;
  background-color: white;
  position: relative;
  color: #000000;
  font-size: 16px;
  text-align: left; }
  .footer__container {
    padding-bottom: 20px; }
  .footer__copy {
    margin: 0;
    padding: 0; }

@media (min-width: 1200px) {
  .footer {
    max-width: 1600px; } }

.site-navigation {
  position: relative; }
  .site-navigation__list {
    list-style: none;
    margin: 0;
    padding: 0;
    color: #000000;
    font-size: 18px;
    line-height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
  .site-navigation__item {
    position: relative;
    text-align: right;
    padding-left: 40px; }
  .site-navigation__link {
    text-decoration: none;
    color: inherit; }

.container {
  margin: 0;
  padding: 0;
  padding-left: 40px;
  padding-right: 40px; }

@media (min-width: 1200px) {
  .container {
    max-width: 1600px; } }

.page-main {
  margin: 0;
  padding: 0;
  background-color: white;
  padding-top: 100px; }
  .page-main__title {
    margin: 0;
    padding: 0;
    margin-bottom: 40px;
    font-size: 18px;
    line-height: 32px;
    font-weight: normal; }

@media (min-width: 768px) {
  .page-main__title {
    margin-bottom: 100px;
    max-width: 80vw;
    font-size: 24px;
    line-height: 36px; } }

@media (min-width: 1200px) {
  .page-main__title {
    margin-bottom: 120px;
    max-width: 60vw;
    font-size: 36px;
    line-height: 56px; } }

.masonry__list {
  margin: 0;
  padding: 0;
  grid-column: span 12; }

.masonry__item {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
  background-color: gray;
  width: 100%; }
  .masonry__item:last-child {
    margin-bottom: 0; }

.masonry img {
  max-width: 100%;
  display: block; }

.masonry__caption {
  margin: 0;
  padding: 0;
  font-weight: 500;
  margin-top: 10px; }

@media (min-width: 768px) {
  .masonry__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 20px; }
  .masonry__list {
    grid-column: span 4; }
    .masonry__list--wide {
      grid-column: span 8; } }

.contact {
  padding-top: 60px;
  padding-bottom: 60px; }
  .contact__title {
    margin: 0;
    padding: 0;
    font-size: 32px;
    margin-bottom: 40px; }
  .contact__item {
    margin: 0;
    padding: 0;
    display: block;
    margin-bottom: 10px; }

@media (min-width: 768px) {
  .contact__wrapper {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-count: 2;
    column-gap: 20px; } }

@media (min-width: 1200px) {
  .contact {
    padding-top: 120px;
    padding-bottom: 120px; } }

.gallery__wrapper {
  background-color: #020685; }

.gallery__list {
  margin: 0;
  padding: 0; }

.gallery__item {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%; }
  .gallery__item img {
    width: 100%;
    display: block;
    margin: 0 auto; }

@media (min-width: 768px) {
  .gallery__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    width: 100%; }
  .gallery__item {
    width: 50%; }
    .gallery__item--wide {
      width: 100%; } }

.page-project__container {
  padding-top: 80px;
  padding-bottom: 40px; }

.page-project__title {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 30px;
  font-weight: 800; }

.page-project__text {
  margin: 0;
  padding: 0;
  font-size: 16px; }

@media (min-width: 768px) {
  .page-project__container {
    padding-top: 120px;
    padding-bottom: 40px; }
  .page-project__description {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px; }
  .page-project__title {
    margin: 0;
    padding: 0;
    grid-column: span 6;
    font-size: 32px;
    line-height: 48px; }
  .page-project__text {
    grid-column: span 6; } }

@media (min-width: 1200px) {
  .page-project__title {
    font-size: 48px;
    line-height: 60px; }
  .page-project__text {
    grid-column: 7 / 12; } }

.selected {
  background-color: white; }
  .selected__container {
    padding-top: 50px;
    padding-bottom: 80px; }
  .selected__title {
    margin: 0;
    padding: 0;
    font-size: 24px;
    line-height: 38px;
    margin-bottom: 30px; }
  .selected__list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .selected__item {
    margin-bottom: 30px; }
    .selected__item:last-child {
      margin: 0; }
    .selected__item img {
      display: block;
      width: 100%; }
  .selected__caption {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 22px;
    margin-top: 10px;
    font-weight: 400; }

@media (min-width: 768px) {
  .selected__container {
    padding-top: 80px;
    padding-bottom: 120px; }
  .selected__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px; }
  .selected__item {
    grid-column: span 4; }
  .selected__title {
    margin: 0;
    padding: 0;
    font-size: 32px;
    line-height: 60px;
    margin-bottom: 40px; }
  .selected__caption {
    font-size: 16px;
    line-height: 26px;
    margin-top: 10px; } }

@media (min-width: 1200px) {
  .selected__item {
    grid-column: span 2; } }
